:root {
  --main-color: #006eff;
  --color-white: #fff;
  --main-text-color: rgba(0,0,0,0.80);
  --secondary-text-color: rgba(0,0,0,0.62);
  --third-text-color: rgba(0,0,0,0.38);
  --darkened-background: rgba(0,0,0,0.025);
  --border: 1px solid #e0e0e0;
  --secondary-border: 1px solid #f5f5f5; }

* {
  box-sizing: border-box; }

html, body {
  height: 100vh;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
  font-size: 17px; }

#root {
  height: 100%; }

.App {
  height: 100%;
  display: grid;
  grid-template-columns: minmax(250px, 1fr) repeat(6, 1fr);
  grid-template-rows: 80px 1fr 60px;
  grid-template-areas: "user room room room room room room" "roomList chat chat chat chat chat chat" "addRoom msg msg msg msg msg msg"; }

.login {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: #fff;
  background-color: var(--color-white);
  color: rgba(0,0,0,0.38);
  color: var(--third-text-color);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  .login .is-logged-in {
    display: none; }
  .login input {
    padding: 15px;
    font-size: 1.3em;
    border: 1px solid #e0e0e0;
    border: var(--border);
    background-color: rgba(0,0,0,0.025);
    background-color: var(--darkened-background);
    color: rgba(0,0,0,0.80);
    color: var(--main-text-color); }
    .login input:focus {
      outline: none; }

.hide-login-component {
  display: none; }

.room-list {
  grid-area: roomList;
  word-break: break-all;
  border-right: 1px solid #e0e0e0;
  border-right: var(--border);
  color: rgba(0,0,0,0.38);
  color: var(--third-text-color);
  overflow-y: auto; }
  .room-list ul {
    list-style: none;
    padding: 0;
    margin: 0; }
    .room-list ul li {
      padding: 18px 15px 18px 22px;
      font-weight: bold;
      border-bottom: 1px solid #f5f5f5;
      border-bottom: var(--secondary-border); }
      .room-list ul li:hover {
        background-color: rgba(0,0,0,0.025);
        background-color: var(--darkened-background);
        color: rgba(0,0,0,0.62);
        color: var(--secondary-text-color);
        cursor: pointer; }

.new-room-form {
  grid-area: addRoom;
  border-top: 1px solid #e0e0e0;
  border-top: var(--border);
  display: flex;
  justify-content: center;
  align-content: center;
  padding: 0 15px; }
  .new-room-form form, .new-room-form input {
    border: none;
    width: 100%;
    height: 100%; }
    .new-room-form form:focus, .new-room-form input:focus {
      outline: none; }

.App__sub-grid {
  grid-area: chat;
  overflow-y: auto;
  display: grid;
  grid-template-rows: 1fr 30px; }

.message-list {
  padding: 25px 0 5px 5px; }
  .message-list.flex-center {
    display: flex;
    justify-content: center;
    align-items: center; }

.typing-indicator {
  background-color: rgba(0,0,0,0.025);
  background-color: var(--darkened-background);
  display: flex;
  align-items: center;
  padding-left: 10px; }

.message-list__empty {
  grid-area: chat;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  .message-list__empty h2 {
    margin-bottom: 0;
    color: rgba(0,0,0,0.38);
    color: var(--third-text-color); }
  .message-list__empty p {
    color: rgba(0,0,0,0.38);
    color: var(--third-text-color); }
  .message-list__empty svg {
    width: 75px;
    fill: #006eff;
    fill: var(--main-color); }

.no-room-selected {
  grid-area: chat;
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgba(0,0,0,0.38);
  color: var(--third-text-color); }

.send-message-form {
  grid-area: msg;
  padding: 0 30px;
  display: flex;
  align-items: center;
  border-top: 1px solid #e0e0e0;
  border-top: var(--border);
  border-left: 1px solid #e0e0e0;
  border-left: var(--border); }
  .send-message-form textarea {
    width: 100%;
    padding: 10px;
    resize: none;
    border: none; }
    .send-message-form textarea:focus {
      outline: none; }
    .send-message-form textarea[disabled] {
      background: transparent; }
  .send-message-form svg {
    width: 32px;
    height: 32px;
    fill: #006eff;
    fill: var(--main-color); }

.message-list__message {
  display: flex;
  padding: 10px 3px 0 3px;
  margin: 10px 0 0 0; }
  .message-list__message:not(:first-of-type) {
    border-top: 1px solid #f5f5f5;
    border-top: var(--secondary-border); }
  .message-list__message img {
    width: 45px;
    height: 45px;
    border-radius: 3px;
    flex-shrink: 0; }

.message-content {
  padding-left: 5px;
  word-break: break-word; }
  .message-content .message-content__sender {
    color: rgba(0,0,0,0.38);
    color: var(--third-text-color);
    margin-bottom: 3px;
    font-size: 0.875em; }
  .message-content .message-content__message {
    line-height: 1.6;
    color: var(--primary-text-color); }

.user {
  grid-area: user;
  border-bottom: 1px solid #e0e0e0;
  border-bottom: var(--border);
  border-right: 1px solid #e0e0e0;
  border-right: var(--border);
  color: rgba(0,0,0,0.38);
  color: var(--third-text-color);
  display: flex;
  align-items: center;
  padding-left: 15px; }
  .user span {
    padding-left: 5px;
    font-weight: bold;
    font-size: 1.2em; }

.current-room {
  grid-area: room;
  border-bottom: 1px solid #e0e0e0;
  border-bottom: var(--border);
  color: rgba(0,0,0,0.62);
  color: var(--secondary-text-color);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 15px;
  font-size: 2em;
  font-weight: bold; }

